<template>
  <v-container id="paper_t" fluid tag="section">
    <remote-js src="https://cdn.bootcss.com/mathjax/2.7.0/MathJax.js?config=TeX-AMS-MML_HTMLorMML"></remote-js>
    <template v-if="!flag8">
      <v-skeleton-loader v-bind="attrs" type="article, actions" height="500px"></v-skeleton-loader>
    </template>

    <template v-else>
      <v-row>
        <v-tabs>
          <v-tab v-for="(item, index) in items" :key="index" @click="choose_year = index">{{ item }}</v-tab>
        </v-tabs>
      </v-row>

      <v-row id="this_p">
        <v-card v-for="(item, index) in q_show" :key="item.description" class="mx-auto" width="750px">
          <v-card-text>
            <div class="text--primary" v-html="(page*5+index-4).toString() + '. ' + q_show[index].description"
              style="font-size: 16px;">
              <!-- {{ index + 1 + '. ' +  }}.{{ paper[choose_year][index].description }} -->
            </div>
            <!-- <div style="margin-top: 5px;">
              {{ paper[choose_year][index].choice[0] }}
            </div> -->
            <div v-if="q_show[index].question_class == 'choice'">
              <div v-html="q_show[index].choice[0].A" style="margin-top: 10px; font-size: 15px;"></div>
              <div v-html="q_show[index].choice[0].B" style="margin-top: 7px; font-size: 15px;"></div>
              <div v-html="q_show[index].choice[0].C" style="margin-top: 7px; font-size: 15px;"></div>
              <div v-html="q_show[index].choice[0].D" style="margin-top: 7px; font-size: 15px;"></div>
            </div>
          </v-card-text>
          <v-card-actions>
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-btn icon
                  @click="show_ans[choose_year][page*5+index-5] = !show_ans[choose_year][page*5+index-5]; key++"
                  v-bind="attrs" v-on="on">
                  <v-icon>{{ show_ans[choose_year][page*5+index-5] ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
                </v-btn>
              </template>
              <span>查看答案</span>
            </v-tooltip>
            <v-spacer></v-spacer>
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-btn icon @click="add_question(choose_year, page*5+index-5); flag10++" v-bind="attrs" v-on="on">
                  <v-icon>mdi-plus</v-icon>
                </v-btn>
              </template>
              <span>添加</span>
            </v-tooltip>
          </v-card-actions>

          <v-expand-transition>
            <div v-show="show_ans[choose_year][page*5+index-5]" :key="key">
              <v-divider></v-divider>
              <v-card-text v-html="'参考答案：' + q_show[index].answer">
                <!-- {{  }} -->
              </v-card-text>
            </div>
          </v-expand-transition>
        </v-card>
      </v-row>

      <!-- 已选题目 -->
      <!-- <v-speed-dial v-model="fab" direction="top" open-on-hover transition="slide-y-reverse-transition" style="position: fixed; top: 545px; right: 50px;">
        <template v-slot:activator> -->
      <v-badge bordered overlap color="warning" :content="selected.length" :key="selected.length"
        style="position: fixed; top: 548px; right: 18px;">
        <v-btn @click="sheet = !sheet; flag11++" color="secondary" depressed fab large dark class="v-btn--example"
          style="position: fixed; top: 550px; right: 15px;">
          已选题目
        </v-btn>
      </v-badge>
      <!-- <v-btn @click="jump" color="secondary" depressed fab large dark class="v-btn--example" style="position: fixed; top: 570px; right: 15px;">
            下一步
          </v-btn> -->
      <!-- </template>
        <v-btn fab dark small color="indigo">
          <v-icon>mdi-plus</v-icon>
        </v-btn>
        <v-btn fab dark small color="red">
          <v-icon>mdi-delete</v-icon>
        </v-btn>
      </v-speed-dial> -->
      <v-bottom-sheet v-model="sheet">
        <v-sheet class="text-center" height="550px">
          <v-row>
            <div style="text-align: left; font-size: 18px; font-weight: bold; margin-left: 20px; margin-top: 25px;">当前组卷</div>
            <v-col cols="12" md="4" style="margin-left: 20px;">
              <v-text-field v-model="name" :value="name" label="试卷名称" rows="1" row-height="20"
                :rules="rules_name" required></v-text-field>
            </v-col>
            <v-col cols="12" md="3" style="margin-left: 30px;">
              <v-text-field v-model="author" :value="author" label="试卷作者" rows="1" row-height="20"
                :rules="rules_author" required></v-text-field>
            </v-col>
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-btn color="warning" fab :loading="loading" @click="submit" v-bind="attrs" v-on="on" style="margin-left: 34px;">
                  <v-icon>mdi-cloud-upload</v-icon>
                </v-btn>
              </template>
              <span>提交</span>
            </v-tooltip>
          </v-row>
          <v-simple-table id="this_s" dense fixed-header height="400px" style="margin-left: 20px; margin-right: 20px;">
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text--primary">题目</th>
                  <th class="text--primary">来源</th>
                  <th class="text--primary">上移</th>
                  <th class="text--primary">下移</th>
                  <th class="text--primary">删除</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in selected" :key="item.description">
                  <td v-html="(index+1).toString() + '. ' + item.description" style="text-align: left;"></td>
                  <td v-html="item.source"></td>
                  <td>
                    <v-btn icon @click="up(index); flag11++">
                      <v-icon>mdi-chevron-up</v-icon>
                    </v-btn>
                  </td>
                  <td>
                    <v-btn icon @click="down(index); flag11++">
                      <v-icon>mdi-chevron-down</v-icon>
                    </v-btn>
                  </td>
                  <td>
                    <v-btn icon @click="del(index); flag11++">
                      <v-icon>mdi-delete</v-icon>
                    </v-btn>
                  </td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
          <!-- <template v-slot:activator="{ on, attrs }">
            <v-btn color="secondary" dark v-bind="attrs" v-on="on">
              已选题目
            </v-btn>
          </template> -->
          <!-- <v-fab-transition>
            <v-btn color="pink" dark top right fab>
              <v-icon>mdi-plus</v-icon>
            </v-btn>
          </v-fab-transition> -->
          
          
          <!-- <v-list id="this_s">
          <v-subheader>已选题目</v-subheader>
          <v-list-item two-line v-for="q in selected" :key="q.description">
            <v-list-item-title v-html="q.description"></v-list-item-title>
          </v-list-item>
        </v-list> -->
        </v-sheet>
      </v-bottom-sheet>
      <v-pagination v-model="page" class="my-4" :length="5"></v-pagination>

    </template>
  </v-container>
</template>

<script>
  export default {
    name: 'paper_t',
    components: {
      'remote-js': {
        render(createElement) {
          return createElement('script', {
            attrs: {
              type: 'text/javascript',
              src: this.src,
              onload: this.onload
            }
          });
        },
        props: {
          src: {
            type: String,
            required: true
          },
        },
      },
    },
    data() {
      return {
        items: [
          "2020年全国一卷", "2019年全国一卷", "2018年全国一卷", "2017年全国一卷", "2016年全国一卷"
        ],
        choose_year: 0,
        paper: [],
        page: 1,
        page_num: '',
        selected: [],
        sheet: false,
        flag8: false,
        key: 0,
        flag10: 0,
        flag11: 0,
        name: '',
        author: '',
        rules_name: [val => (val || '').length > 0 || '名称不能为空'],
        rules_author: [val => (val || '').length > 0 || '作者不能为空'],
        loading: false,

      }
    },
    created() {
      var data = new Array()
      for (var i = 0; i < 5; i++) {
        data[i] = new Array()
        for (var j = 0; j < 21; j++) {
          data[i][j] = false
        }
      }
      this.show_ans = data
      this.$global2.load_config()
      this.$global2.load_css()
      this.get_paper(2020, 2016)
    },
    computed: {
      q_show: function () {
        return this.paper[this.choose_year].slice(this.page * 5 - 5, this.page * 5)
      },
      // des: function (index) {
      //   var n = this.page * 5 + index - 4;
      //   return n.toString() + '. ' + this.q_show[index].description
      // }
    },
    methods: {
      get_paper: function (y1, y2) {
        var that = this;
        if (y1 >= y2) {
          that.$axios({
            method: 'get',
            url: '/api/get_paper_by_year/',
            params: {
              year: y1
            }
          })
          .then(function (response) {
            if (response.data.error_num == 0) {
              console.log(response.data)
              that.paper[2020 - y1] = response.data.question
              y1--
              that.get_paper(y1, y2)
            } else if (response.data.error_num == 1) {
              console.log(y1, "年，获取失败！")
            }
          })
        } else if (y1 < y2) {
          setTimeout(() => {
            that.flag8 = true
            that.$nextTick(function () {
              that.$global2.MathQueue("this_p");
            })
          }, 1000)
          console.log("完成", that.paper)
        }
      },
      add_question: function (year, index) {
        this.selected.push(this.paper[year][index])
        this.selected[this.selected.length - 1].source = (2020 - year).toString() + '-' + (index + 1).toString()
      },
      up: function (index) {
        if (index != 0) {
          this.selected[index] = this.selected.splice(index - 1, 1, this.selected[index])[0];
        } else {
          this.selected.push(this.selected.shift());
        }
      },
      down: function (index) {
        if (index != this.selected.length - 1) {
          this.selected[index] = this.selected.splice(index + 1, 1, this.selected[index])[0];
        } else {
          this.selected.unshift(this.selected.splice(index, 1)[0]);
        }
      },
      del: function (index) {
        this.selected.splice(index, 1)
      },
      submit: function () {
        var that = this;
        this.loading = true;
        var data = new Array();
        for (var i = 0; i < that.selected.length; i++) {
          data[i] = that.selected[i].id
        }
        console.log(data)
        if (that.selected.length != 0 && that.name && that.author) {
          this.$axios({
            method: 'get',
            url: '/api/make_paper/',
            params: {
              tec_id: that.$global.userMsg.id,
              name: that.name,
              author: that.author,
              q_num: that.selected.length,
              q_id: JSON.stringify(data),
            }
          })
          .then(function (response) {
            if (response.data.error_num == 0) {
              console.log(response.data)
              that.selected = [];
              that.loading = false;
              alert("提交成功！")
            } else if (response.data.error_num == 1) {
              alert("提交失败！")
            }
          })
        } else {
          alert("试卷名称、作者、选题不能为空！")
        }
        setTimeout(() => {
          that.loading = false;
        }, 3000)
      }
    },

    watch: {
      choose_year: {
        handler(newVal, oldVal) {
          var that = this;
          that.page = 1;
          console.log("choose_year = ", 2020 - oldVal, 2020 - newVal)
          that.$nextTick(function () {
            that.$global2.MathQueue("this_p");
          })
        },
        deep: true,
        immediate: true
      },
      page: {
        handler(newVal, oldVal) {
          var that = this;
          console.log("page = ", oldVal, newVal)
          that.$nextTick(function () {
            that.$global2.MathQueue("this_p");
          })
        },
        deep: true,
        immediate: true
      },
      flag10: {
        handler(newVal, oldVal) {
          var that = this;
          console.log("flag10 = ", oldVal, newVal)
          console.log(that.selected)
          that.$nextTick(function () {
            that.$global2.MathQueue("this_s");
          })
        },
        deep: true,
        immediate: true
      },
      flag11: {
        handler(newVal, oldVal) {
          var that = this;
          console.log("flag11 = ", oldVal, newVal)
          that.$nextTick(function () {
            that.$global2.MathQueue("this_s");
          })
        },
        deep: true,
        immediate: true
      },
      key: {
        handler(newVal, oldVal) {
          var that = this;
          console.log("key = ", oldVal, newVal)
          that.$nextTick(function () {
            that.$global2.MathQueue("this_p");
          })
        },
        deep: true,
        immediate: true
      },
    },
  }
</script>